import React, { useState } from 'react';

import './Navigation.css';

function Navigation(props: any) {
  const { mobile } = props;
  const [open, setOpen] = useState(true)
  const navItem = (text: string, linkId: string) => {
    return <a className="Nav-Link" href={`#${linkId}`} key={text}>{text}</a>
  }

  const sections = [
    {
      text: 'Next Pickup',
      linkId: 'Hours'
    },
    {
      text: 'Location',
      linkId: 'Address'
    },
    {
      text: 'About',
      linkId: 'About'
    },
  ];
  return (
    <nav id="Navigation">
      <div className={`Navigation-Container ${open && 'Open'}`}>
        { sections.map(s => navItem(s.text, s.linkId))}
        {/* { mobile && <button id='NavigationToggle' onClick={e => setOpen(!open)}><img className='Hamburger' alt="Open Navigation" src="data:image/png;base64,"/></button>} */}
      </div>
    </nav>
  );
}

export default Navigation;
